import React from 'react';
import { render } from '@testing-library/react';
import Search from '../component/Search';
import ResultWorld from '../component/resultWorld';
import covid19totalapi from '../api/covid19totalapi';

class Total extends React.Component{
    state = {result: [],
        name:""   
       };

            onSearchSubmit = async () => {
            const response = await covid19totalapi.get("", {
                "params":{
                    "format":"json"
                    }
            }); 
            if(response){
                this.setState({name:"world"});
                console.log(response.data[0]);
                this.setState({result: response.data[0]});
            } else {
                alert("Check Country Name");
            }
            }
    
    render(){
        return(
            <div>
                <div className="Result">
                    <ResultWorld name={this.state.name} data={this.state.result} onLoad={this.onSearchSubmit()} />
                </div>
            </div>
        );
    }
}

export default Total;

