import React from 'react';
import { render } from '@testing-library/react';
import Search from '../component/Search';
import Result from '../component/Result';
import covid19api from '../api/covid19api';

class Countries extends React.Component{
    state = {result: [],
        name:""   
       };

            onSearchSubmit = async (term) => {
            const response = await covid19api.get("", {
                "params":{
                    "region":`${term}`
            }
            }); 
            if(response){
                this.setState({name: term});
                this.setState({result: response.data.data.summary});
            } else {
                alert("Check Country Name");
            }
            }
    
    render(){
        return(
            <div>
                <div className="search">
                    <Search onSubmit ={this.onSearchSubmit}/>
                </div>
                <div className="Result">
                    <Result name={this.state.name} data={this.state.result} />
                </div>
            </div>
        );
    }
}

export default Countries;

